import React, { useEffect, useState } from 'react';
import SplashScreen from './splashScreen/SplashScreen';
import { splashScreen } from '../portfolio';
import { StyleProvider } from '../contexts/StyleContext';
import { useLocalStorage } from '../hooks/useLocalStorage';

const Main: React.FC = () => {
  const darkPref = window.matchMedia('(prefers-color-scheme: dark)');
  const [isDark, setIsDark] = useLocalStorage<boolean>('isDark', darkPref.matches);
  const [isShowingSplashAnimation, setIsShowingSplashAnimation] = useState<boolean>(true);

  useEffect(() => {
    if (splashScreen.enabled) {
      const splashTimer = setTimeout(
        () => setIsShowingSplashAnimation(false),
        splashScreen.duration,
      );

      return () => {
        clearTimeout(splashTimer);
      };
    }
  }, []);

  const changeTheme = () => {
    setIsDark(!isDark);
  };

  return (
    <div className={isDark ? 'dark-mode' : undefined}>
      <StyleProvider value={{ isDark: isDark, changeTheme: changeTheme }}>
        {isShowingSplashAnimation && splashScreen.enabled ? <SplashScreen /> : <SplashScreen />}
      </StyleProvider>
    </div>
  );
};

export default Main;
